:root {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  font-family: 'Montserrat', sans-serif;
}

body {
  overflow: scroll;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.logo {
  position:absolute;
  top: 16px;
  left: 56px;
  font-size: calc(30px + 2vmin);
  font-weight: 900;
  text-transform: uppercase;
}

@media screen and (max-width: 760px) {
  .logo {
    left: 24px;
  }
}

.header {
  position: fixed;
  top:0;
  left: 0;
  background-color: #000;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  z-index: -1;
}

.header__video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.icons {
  width: 30px;
  height: 30px;
  color: #fff;
  transition: .2s ease-out;
}

.header__hero {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
	scroll-snap-align: start;
}

.header__small {
  text-transform: uppercase;
  color: #fff;
}

.header__title {
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  margin: 0 0 16px 0;
}

.header__icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 200px;
  padding: 16px;
}

.icons:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.button {
  border: solid 3px #fff;
  border-radius: 4px;
  background: transparent;
  color: #fff;
  padding: 16px;
  cursor: pointer;
  transition: .2s ease-out;
}

.button:hover {
  background: #fff;
  color: #1d1815;
}

.technologies__section {
  display: flex;
  flex-direction: column;
  display: flex;
  text-align: left;
}

.technologies__section-title {
  font-size: .8rem;
  font-weight: 700;
  margin-top: 6px;
}

.technologies__list li {
  margin-top: 16px;
}

@media screen and (max-width: 760px) {
  .technologies__section {
    width: 80%;
    margin: auto;
  }
}

.technologies {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 16px;
  color: #fff;
  scroll-snap-align: start;
  padding-top: 100px;
}

.technologies__title {
  font-size: 1.2rem;
  font-weight: normal;
  margin-top: 0;
}

@media screen and (max-width: 760px) {
  .technologies {
    height: auto;
    grid-template-columns: 1fr;
  }
}

.work-experience__section {
  grid-column-start: 2;
  grid-column-end: span 3;
}

.work-experience__section-title {
  margin-top: 0;
  text-align: left;
  font-weight: 500;
}

.work-experience__description {
  width: 80%;
  text-align: left;
  line-height: 26px;
}

.work-experience__company {
  font-size: .9rem;
  font-weight: 700;
}

@media screen and (max-width: 760px) {
  .work-experience__section {
    width: 80%;
    margin: auto;
    grid-column-start: 1;
    grid-column-end: 1;
  }
}

.work-experience {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 16px;
  color: #fff;
  scroll-snap-align: start;
  padding-top: 100px;
}

.work-experience__title {
  font-size: 1.1rem;
  font-weight: normal;
  margin-top: 0;
}

@media screen and (max-width: 760px) {
  .work-experience {
    grid-template-columns: 1fr;
    height: auto;
  }
}

.icon-scroll {
  position: fixed;
}
.icon-scroll:before {
  position: absolute;
}
.icon-scroll {
  width: 20px;
  height: 45px;
  bottom: 16px;
  right: 16px;
  box-shadow: inset 0 0 0 1px #fff;
  border-radius: 25px;
}
.icon-scroll:before {
  content: '';
  width: 8px;
  height: 8px;
  background: #fff;
  top: 8px;
  left: 6px;
  border-radius: 4px;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: scroll;
          animation-name: scroll;
}

@-webkit-keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(26px);
            transform: translateY(26px);
  }
}

@keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(26px);
            transform: translateY(26px);
  }
}

.App {
  overflow: scroll;
  height: 100vh;
  text-align: center;
  -webkit-scroll-snap-type: mandatory;
      -ms-scroll-snap-type: mandatory;
          scroll-snap-type: mandatory;
  -webkit-scroll-snap-points-y: repeat(100vh);
      -ms-scroll-snap-points-y: repeat(100vh);
          scroll-snap-points-y: repeat(100vh);
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
}

.App-link {
  color: #61dafb;
}

